<template>
<div id="register">
    <el-form label-width="80px">
        <el-form-item label="name">
            <el-input v-model="user.name" placeholder="your name"></el-input>
        </el-form-item>
        <el-form-item label="password">
            <el-input  type="password" v-model="user.password" placeholder="password"></el-input>
        </el-form-item>
        <el-form-item label="confirm">
            <el-input type="password" v-model="state.confirm"></el-input>
            <span v-if="state.confirmsame===false" style="color: red">密码不一致</span>
        </el-form-item>
    </el-form>   
    <span >
        <el-button v-on:click="cancel">取 消</el-button>
        <el-button type="primary"
                    :disabled="state.confirmsame===false"
                    v-on:click="success"
                    >确 定</el-button>
    </span>
</div>
</template>


<script>
export default {
    name:"RegisterBoard",
    props:{
        state:{
            type: Object,
            default: ()=>{
                return{
                    confirm: "",
                    confirmsame: false
                }
            }
        }
    },
    data(){
        return{
            user:{
                name:"",
                password:"",
                confirm:""
            }
        }
    },
    methods:{

    },
    watch: { // 用于实时检测username是否合法
    "state.confirm": {
      handler() {
          this.state.confirmsame = (this.user.password==this.state.confirm)
        // this.state.username_valid = /^[A-Za-z\u4e00-\u9fa5][-A-Za-z0-9\u4e00-\u9fa5_]*$/.test(newName)
      }
    }
  }
}
</script>